import React, { useEffect, useState } from 'react';
import './App.css';
import { Route, Switch, useLocation } from 'react-router-dom';
import HomePage from 'pages/HomePage';
import Header from 'components/Header';
import Sidebar from 'components/Sidebar';
import SidebarRight from 'components/SidebarRight';
import VideoPage from 'pages/VideoPage';
import Page404 from 'pages/404Page';
import { createMuiTheme, makeStyles, ThemeProvider } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';

const mainTheme = createMuiTheme({
  palette: {
    primary: {
      main: '#4B6EE2',
    },
    secondary: {
      main: '#9978DC',
    },
    background: {
      main: '#F3F3F3',
    },
    border: {
      main: '#637ee0',
    },
  },
});

const useStyles = makeStyles(() => ({
  root: {
    backgroundColor: '#F3F3F3',
  },
  content: {
    marginTop: '75px',
    paddingTop: '25px',
  },
  rounded: {
    position: 'fixed',
    pointerEvents: 'none',
    top: '75px',
    boxShadow: '0 0 0px 20px #fff',
    minHeight: 'calc(100vh - 75px)',
    borderRadius: '2.5% 2.5% 0 0',
    borderColor: '#637ee0',
    borderWidth: '1px',
    borderStyle: 'solid',
  },
  inner: {
    width: 'calc(100% - 190px)',
    margin: 'auto',
  },
  fullWidth: {
    width: '100%',
    margin: 'auto',
  },
  collapsed: {
    width: 'calc(100% - 265px)',
    margin: 'auto',
    marginLeft: '165px',
  },
}));

function App() {
  const classes = useStyles();
  const location = useLocation();
  const [crutchLocation, setCrutchLocation] = useState(false);
  const [collapsedSidebar, setCollapsedSidebar] = useState(true);

  useEffect(() => {
    if (crutchLocation) {
      window.location.reload(false);
    } else {
      setCrutchLocation(true);
    }
  }, [location]);

  return (
    <div>
      <ThemeProvider theme={mainTheme}>
        <Header />
        <Grid container className={classes.content}>
          <Switch>
            <Route exact path="/">
              <Grid item className={collapsedSidebar ? classes.inner : classes.collapsed}>
                <HomePage />
              </Grid>
            </Route>
            <Route exact path="/video">
              <Grid item className={classes.fullWidth}>
                <VideoPage />
              </Grid>
            </Route>
            <Route exact path="/playlists" component={Page404}>
              <Grid item className={collapsedSidebar ? classes.inner : classes.collapsed}>
                <Page404 />
              </Grid>
            </Route>
            <Route exact path="/categories">
              <Grid item className={collapsedSidebar ? classes.inner : classes.collapsed}>
                <Page404 />
              </Grid>
            </Route>
            <Route exact path="/curses">
              <Grid item className={collapsedSidebar ? classes.inner : classes.collapsed}>
                <Page404 />
              </Grid>
            </Route>
          </Switch>
        </Grid>
        <Sidebar collapsed={collapsedSidebar} setCollapsed={setCollapsedSidebar} />
        <SidebarRight />
        <Grid container className={classes.rounded} />
      </ThemeProvider>
    </div>
  );
}

export default App;
