import React from 'react';
import { useHistory } from 'react-router-dom';
import { Button, Grid } from '@material-ui/core';
import {
  Dehaze,
  NightsStay,
  FilterNone,
  FormatListBulleted,
  School,
  Home,
} from '@material-ui/icons';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    position: 'fixed',
    top: '0',
    left: '0',
    height: '100%',
    width: '150px',
    overflow: 'hidden',
  },
  collapsed: {
    width: '75px',
  },
  menuBlock: {
    marginBottom: theme.spacing(1),
    backgroundColor: '#F3F3F3',
    borderRadius: '0 8px 8px 0',
    borderColor: '#637ee0',
    borderWidth: '1px',
    borderStyle: 'solid',
    textAlign: 'center',
    padding: theme.spacing(0.5),
  },
  menuBlockMain: {
    paddingTop: '100px',
  },
  menuBlockSecondary: {
    height: '100vh',
  },
  menuButton: {
    marginBottom: '8px!important',
  },
}));

function Sidebar({ collapsed, setCollapsed }) {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Grid className={collapsed ? [classes.root, classes.collapsed] : classes.root} container>
      <Grid item xs="12" className={[classes.menuBlock, classes.menuBlockMain]}>
        <Button
          fullWidth
          className={classes.menuButton}
          color="primary"
          size="small"
          onClick={() => setCollapsed(!collapsed)}
        >
          <Dehaze />
        </Button>
        <Button
          fullWidth
          className={classes.menuButton}
          variant="contained"
          color="primary"
          size="small"
          onClick={() => history.push('/')}
        >
          <Home />
          {!collapsed && 'Главная'}
        </Button>
        <Button
          fullWidth
          className={classes.menuButton}
          variant="contained"
          color="primary"
          size="small"
          onClick={() => history.push('/playlists')}
        >
          <FilterNone />
          {!collapsed && 'Плейлисты'}
        </Button>
        <Button
          fullWidth
          className={classes.menuButton}
          variant="contained"
          color="primary"
          size="small"
          onClick={() => history.push('/categories')}
        >
          <FormatListBulleted />
          {!collapsed && 'Категории'}
        </Button>
        <Button
          fullWidth
          className={classes.menuButton}
          variant="contained"
          color="primary"
          size="small"
          onClick={() => history.push('/curses')}
        >
          <School />
          {!collapsed && 'Курсы'}
        </Button>
      </Grid>
      <Grid item xs="12" className={[classes.menuBlock, classes.menuBlockSecondary]}>
        <Button
          fullWidth
          className={classes.menuButton}
          variant="contained"
          color="primary"
          size="small"
        >
          <NightsStay />
          {!collapsed && 'Тема'}
        </Button>
      </Grid>
    </Grid>
  );
}

export default Sidebar;
